*{
    box-sizing: border-box;
}
body{
    margin: 0;
}

div>nav{
    height: 70px;
    display: flex;
    padding: 10px 10px;
    box-shadow: 0px 0.3px 5.4564px rgb(0, 0, 0.1);
}
nav>.navLogo{
    width: 70px;
}
nav>h2{
    color: #ff5252;
    padding-left: 5px;
    padding-top: 5px;
}
section{
    padding: 10px;
}

hero{
    /* display: flex;
    flex-direction:column ;
   flex-wrap: wrap; */
   display: grid;
   grid-template-rows:auto;
}

.container{
    display: flex;
    justify-content:space-evenly;
	flex-wrap: wrap;
}
section{
    display: flex;
    /* flex-direction: column; */
    justify-content: center;
    /* flex-wrap: wrap; */
  }
  section > img{
    width: 350px;
    height: 300px;
    border-radius: 20px;
  }
hero>h1{
    justify-self: center; 
}
hero>p{
    justify-self: center; 

}

.cards{
    width: 175px;
    height: 220px;
    font-size: 14px;
    padding: 10px;
    position:sticky;

}
.cards>img{
    width: 100%;
    height: 100%;
    border-radius: 20px;  
}
.cards>.span{
    background-color: #ff5252;
    position: fixed;
}
.cardBadge{
    position:absolute;
    top: 15px;
    left: 15px;
    background-color: white;
    padding:5px 7px;
    border-radius: 5px;
    font-weight: bold;
}