#FormMain{
    border: 2px solid #333;
    box-shadow: inset 10px 10px 7px -5px rgb(0 , 0, 0, 0.7);

    width: max-content;
    height: max-content;
    margin: 5px;
    padding: 5px;
}
input{
    display: flex;
    align-content: center;
    margin: 10px;
    /* margin-left: 38%; */
    border-radius: 5px;
}
textarea ,#heading,button{
    /* margin-left: 38%; */
    border-radius: 5px;
    margin: 10px;

}
select,label{
    margin: 10px;
    border-radius: 5px;

}
p {
    width: max-content;
}

.radioGroup{
    display: flex;
    flex-direction: row;
}

